import $ from 'jquery';
import shuffle from 'shufflejs';

export default class CustomShuffle {
    constructor(element, filter_fields, props) {
        this.shuffle = new Shuffle(element, props);

        this.filterName = null;
        this.filters = {};
        for (let i = 0; i < filter_fields.length; i++) {
            this.filters[filter_fields[i]] = [];
        }

        this._bindEventListeners();
    }

    _bindEventListeners() {
        let shuffle = this;
        $('.filter-item').click(function(e) {
            let name = $(this).parent().data('name'),
                value = $(this).data('value');

            let index = shuffle.filters[name].indexOf(value);
            if (index > -1) {
                shuffle.filters[name].splice(index, 1);
            } else {
                shuffle.filters[name].push(value);
            }

            shuffle.filterName = name;
            shuffle.filter();
        });
    }

    _handleSearchKeyup(evt) {
        var searchText = evt.target.value.toLowerCase();

        this.shuffle.filter(function (element, shuffle) {
            let titleText = $(element).find('.image-top-label').text().toLowerCase().trim();

            return titleText.indexOf(searchText) !== -1;
        });
    }

    filter() {
        if (this.hasActiveFilters()) {
            this.shuffle.filter(this.itemPassesFilters.bind(this));
        } else {
            this.shuffle.filter(Shuffle.ALL_ITEMS);
        }
    }

    itemPassesFilters(element) {
        let res = true;
        
        for (let key in this.filters) {
            let value = $(element).data(key);
            
            if (this.filters[key].length) {
                let filter = this.filters[key];

                if (filter.length > 0 && filter.indexOf(value) < 0) {
                    res = false;
                    break;
                }

            }
        }
        return res;
    }

    hasActiveFilters() {
        return Object.keys(this.filters).some(function (key) {
            return this.filters[key].length > 0;
        }, this);
    }
}