import $ from 'jquery';
import CustomShuffle from './customShuffle';

export default class FilterBlock {
    constructor(element) {
        this.el = $(element);
        this.search = this.el.find('svg.search');
        this.input = this.el.find('.search-input');
        this.layout = this.el.find('.search-layout');

        this.initSearchEvents();
        this.initFilterEvents();
        this.initShuffle();        
    }

    initShuffle() {
        let parent = this;
        let filterFields = $.map($('.filter-body-elements'), el => $(el).data('name'));

        this.shuffleInstance = new CustomShuffle($('.grid-images'), filterFields, {
            itemSelector: '.picture-item',
            sizer: '.my-sizer-element',
            buffer: 1
        });

        this.input.blur(function() {
            if(parent.input.val().length > 0) {
                return false;
            } else {
                //$searchTrigger.removeClass('active');
            }
        });
       
        this.el.find('.search-clear').click(function() {
            parent.input.val('');
            parent.shuffleInstance.filter(Shuffle.ALL_ITEMS);
        });

        this.input.focus(function() {
            //$searchTrigger.addClass('active');
        });

        
        this.input.on('keyup', this.shuffleInstance._handleSearchKeyup.bind(this));
    }

    initSearchEvents() {
        let parent = this;
        parent.layout.click(function(e) {
            if (!parent.search.hasClass('active')) {
                parent.search.addClass('active');
            }

            setTimeout(() => {
                parent.input.focus();
            }, 300)
        });

        parent.search.click(function(e) {
            if (parent.search.hasClass('active')) {
                setTimeout(() => {
                    parent.input.focus();
                }, 200)
            }
        });

        $(document).click(function(e) {
            if (!$.contains(parent.search.parent()[0], e.target) && parent.input.val().length === 0) {
                parent.search.removeClass('active');
                parent.search.removeClass('searching');
                parent.input.val('');
            }
        });

        parent.input.keyup(function(e) {
            if (e.keyCode === 13) {
                parent.input.blur();
            }
        })
        
        $(document).on('input', parent.input, function() {
            if (parent.input.val().length > 0) {
                parent.search.addClass('searching');
            } else {
                parent.search.removeClass('searching');
            }
        });
    }

    initFilterEvents() {
        let parent = this;
        let navigator = $('.filter-navigator'), body = $('.filter-body');
        navigator.css({'margin-top': parseInt($('figure:first').css('margin-top')) + 'px'});

        $('.icon-filter').click(function(e) {
            if (navigator.is(':hidden')) {
                navigator.slideDown('fast');
            }
            else {
                parent.hideFilter();
            }
        });

        $(document).click(function(e) {
            if ($(e.target).closest('.filter-navigator').length > 0
                || $(e.target).closest('.filter-body').length > 0
                ||$(e.target).closest('#filter-block-head').length > 0) {
               return 0;
            }
            parent.hideFilter();
        });

        navigator.find('.filter-nav-item').click(function(e) {
            if (navigator.find('> div.active').attr('id') == $(this).attr('id')) {
                return false;
            }
            body.find('> div.active').hide().removeClass('active');
            navigator.find('> div.active').removeClass('active');
    
            let position = $(this).parent().position();
            let offsetTop = $(this).position().top + parseInt($(this).parent().css('marginTop'));
    
            let elem = $('#' + $(this).attr('id').replace('nav', 'body'));
            elem.parent().css({'visibility': 'hidden', 'width': 'fit-content'});
    
            $(this).addClass('active');
            elem.show().addClass('active').css({'width': 'fit-content'});
            let elemWidth = elem.data('width') ? elem.data('width') : (Math.ceil(elem.width()) + parseInt(elem.css('padding-left')) * 2);
    
            elem.css({'width': elemWidth + 'px'});
            if (!elem.data('width')) {
                elem.data('width', elemWidth);
            }
    
            elem.parent().css({'top': position.top + offsetTop, 'left': position.left, 'width': 0, 'visibility': 'visible'});
            elem.parent().animate({'left': position.left - elemWidth + 1, 'width': elemWidth + 'px'}, 400);
        });
    
        $('.filter-item').click(function(e) {
            $(this).toggleClass('active');
    
            let parent = $(this).parent();
            let navItem = $('#' + parent.attr('id').replace('body', 'nav'));
            if (parent.children().hasClass('active') && !navItem.hasClass('active-item')) {
                navItem.addClass('active-item');
            } else {
                navItem.removeClass('active-item');
            }
        });
    
        /*$('select.selectize-element').selectize();
    
        $('input.tags-input').selectize({
            delimiter: ',',
            persist: false,
            maxItems: 8,
            create: true
        });*/
    
        let isFocused = false;
        $('.complex-input').focus(function(e) {
            $(this).hide();
            $(this).next().removeClass('d-none').find('input:first').focus();
        });

        $('.complex-input-block').find('input').blur(function(e) {
            if (!$(e.relatedTarget).closest('.complex-input-block').length) {
                let block = $(this).closest('.complex-input-block'), smallValues = [];
                $(this).closest('.complex-input-block').find('input').each(function() {
                    smallValues.push($(this).val() ? $(this).val() : '0');
                });
                block.addClass('d-none');
                block.prev().val(smallValues.join('x')).show();
            }
        })
    }

    hideFilter() {
        let navigator = $('.filter-navigator');
        navigator.slideUp('fast');
        $('.filter-body > div.active').hide().removeClass('active');

        navigator.find('> div.filter-nav-item.active').each(function(i, elem) {
            let id = $(elem).attr('id').replace('nav', 'body');
            if (!$('#' + id + ' > div.active').length) {
                $(this).removeClass('active');
            }
        });
    }
}