import $ from 'jquery';


function logout() {
    let $form = $(this).closest('form');
    $.post($form.attr('action'), $form.serialize()).done(function() {
        location.reload();
    });
    return false;
}

function login() {
    let form = $('#' + ($('#signup-tab').hasClass('active') ? 'signup' : 'signin') + '-block').children('form');

    $.post($(form).attr('action'), $(form).serialize())
        .done(function(data) {
            if ($(form).find('input[name="is_artist"]').prop("checked")) {
                window.location = '/users/' + data.user + '/edit';
            } else {
                location.reload();
            }
        })
        .fail(function(data) {
            let json = data.responseJSON, exist_fields = [];
            $(form).find('input.form-control').each(function(i, input) {
                let is_invalid = $(input).attr('name') in json;
                $(input).removeClass('is-invalid is-valid').addClass(is_invalid ? 'is-invalid' : 'is-valid');

                if (is_invalid) {
                    exist_fields.push($(input).attr('name'));
                    $(input).next().text(json[$(input).attr('name')]);
                }
            });

            $(form).find('.error-messages').html('')
            for (key in json) {
                if (!(key in exist_fields)) {
                    $(form).find('.error-messages').append(json[key]);
                }
            }
        });
    return false;
}

$('#modal-auth button[type=submit]').click(login);

$('#modal-auth input').keyup(function(e) {
    if (e.which === 13) {
        let nextInput = $(this).closest('.row').next().find('input');
        if (nextInput.length) {
            nextInput.focus();
        } else {
            login();
        }
    }
});

$(document).on('click', '.oi-account-login', function (e) {
    let modal = $('#modal-auth');

    //modal.find('.modal-title').text(block.data('title'))
    //modal.find('form').attr('action', block.data('action'))
    //modal.find('.to_action').text(block.data('btn'))
    modal.modal();
});

$('#logout-form button').click(logout);


